h1,
h2,
h3,
h4,
p,
ul,
body {
  margin: 0px;
  padding: 0px;
}

img {
  display: block;
  max-width: 100%;  
  height: auto;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}
  
input,
textarea {
  outline: none;
}

  button {
  border: none;
}